import './Button.scss'

function Button(props: { children: any; variant: string; type: string, onClick?: any, file?: boolean }) {
  const { children, variant, type, onClick, file } = props
  
  return file ?
  <label className={`button button--${variant} button--${type}`}>
      <input 
        type="file"
        onChange={onClick}
      />
      {children}
  </label>
  : 
  <button onClick={onClick ? onClick : null} className={`button button--${variant} button--${type}`}>
      {children}
  </button>
}

export default Button
