import { ApplicationState, Status } from '../../types'
import { createSelector } from 'reselect'

export const getAllIssues = (state: ApplicationState) => state.ui.issues
export const getIssue = (state: ApplicationState, id: number) => state.ui.issues.find(issue => issue.id === id)
export const getIssuesStatus = (state: ApplicationState, status: Status) => status === Status.all ? state.ui.issues :
  state.ui.issues.filter(issue => issue.status === status)
  
export const getTopLoader = createSelector<ApplicationState, boolean, boolean>(
  [(state: ApplicationState) => state.ui.topLoader],
  topLoader => topLoader
)

export const getMainLoader = createSelector<ApplicationState, boolean, boolean>(
  [(state: ApplicationState) => state.ui.mainLoader],
  mainLoader => mainLoader
)
