import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { ReactComponent as Back } from '../../assets/back.svg'
import { ReactComponent as Plus } from '../../assets/plus.svg'
import HeaderNav from '../common/header-nav/HeaderNav'
import { Link } from 'react-router-dom'
import Card from '../common/card/Card'
import Button from '../common/button/Button'
import TopLoader from '../common/top-loader/TopLoader'
import MainLoader from '../common/main-loader/MainLoader'
import { Issue, ApplicationState, Status, StatusText, PlatformText } from '../../types'
import { LIMIT } from '../../constants'
import { loadIssues, resetIssueSuccessCreated } from '../../redux/actions/ui'
import { getIssuesStatus } from '../../redux/selectors/ui'
import Alert from '../common/alert/Alert'

export default function Issues() {
  const [type, setType] = React.useState<Status>(Status.all)
  // const [load, setLoad] = React.useState(true)
  const dispatch = useDispatch()
  const issues = useSelector<ApplicationState, Array<Issue>>(state => getIssuesStatus(state, type))
  const offset = useSelector<ApplicationState, number>(state => state.ui.offset)
  const issueSuccessCreated = useSelector<ApplicationState, boolean>(state => state.ui.issueSuccessCreated)
  const lastIssueNumber = useSelector<ApplicationState, number | undefined>(state => state.ui.lastIssueNumber)

  // useEffect(() => {
  //   if (!load) return
  //   setLoad(false)
  //   dispatch(loadIssues({ offset: 0, limit: LIMIT }))
  // }, [dispatch])

  const handleSetType = (type: Status) => {
    setType(type)
  }

  const handleLoadIssues = () => {
    dispatch(loadIssues({ offset, limit: LIMIT }))
  }

  return (
    <div className="app-layout">
      <header className="app-layout__header">
        <HeaderNav>
          <Link className="header-nav-btn header-nav-btn--icon" to="/">
            <Back />
          </Link>

          <strong className="header-nav__title">Мои обращения</strong>

          <Link className="header-nav-btn header-nav-btn--icon" to="/ticket-new">
            <Plus />
          </Link>
        </HeaderNav>
      </header>
      <MainLoader />
      <main className="app-layout__main">
        <nav className="app-tabs">
          <Button variant="primary" type={type === Status.all ? "default" : "tab" } onClick={()=> handleSetType(Status.all)}>
            {StatusText.all}
          </Button>
          <Button variant="primary" type={type === Status.processing ? "default" : "tab" } onClick={()=> handleSetType(Status.processing)}>
            {StatusText.processing}
          </Button>
          <Button variant="primary" type={type === Status.closed ? "default" : "tab" } onClick={()=> handleSetType(Status.closed)}>
            {StatusText.closed}
          </Button>
        </nav>
        <section className="app-card-grid">
          <TopLoader />
          {issues.map(issue => (
            <Card key={issue?.id} link={`/ticket-details/${issue?.id}`} closed={issue.status === Status.closed}>
              <header className="card__header">
                <h3 className="app-title app-title--h3">№ {issue?.id}. {issue.message}</h3>
              </header>
              <dl className="app-definition">
                <dt>
                  <span className="app-text app-text--desc">Платформа</span>
                </dt>
                {issue.platform && (<dd>
                  <span className="app-text">{PlatformText[issue.platform]}</span>
                </dd>)}
                <dt>
                  <span className="app-text app-text--desc">Создан</span>
                </dt>
                {issue.created && (<dd>
                  <span className="app-text">{issue.created}</span>
                </dd>)}
                <dt>
                  <span className="app-text app-text--desc">Статус</span>
                </dt>
                <dd>
                  <span className="app-text">
                    <strong>{issue?.status && StatusText[issue?.status]}</strong>
                  </span>
                </dd>
              </dl>
            </Card>
          ))}
          {type === Status.all ? <div style={{textAlign: "center"}}>
            <Button variant="primary" type="default" onClick={()=> handleLoadIssues()}>
              ЕЩЕ
            </Button>
            </div>: null}
            {!issues.length ? (
              <div className="notifications-empty">
                <span className="app-text app-text--desc">У вас нет заявок</span>
              </div>
            ) : null}
        </section>
        { issueSuccessCreated && (<Alert>
        <h2 className="app-title app-title--h4">Обращение отправлено</h2>
          <p className="app-text app-text--divider">
            Ваше обращение зарегистрировано под №{lastIssueNumber} и отправлено на рассмотрение.
            Статус обращения вы найдёте в "Моих обращениях"
          </p>
          <footer className="alert__footer">
            <Button variant="primary" type="outline" onClick={() => { dispatch(resetIssueSuccessCreated()) }}>
              OK
            </Button>
          </footer>
        </Alert>
        )}
      </main>
    </div>
  )
}
