import { FC } from 'react'
import { ReactComponent as Back } from '../../assets/back.svg'
import { Link } from 'react-router-dom'
import HeaderNav from '../common/header-nav/HeaderNav'
import { useDispatch } from 'react-redux'
import { addIssue } from '../../redux/actions/ui'
import dayjs from 'dayjs'
import { Platform, PlatformText, Status } from '../../types'
interface Props {
  handleGoBackNewIssue: any,
  platform?: Platform,
  description?: string,
  details?: string,
  attachments?: [],
}

const IssueConfirmation:FC<Props> = ({
  handleGoBackNewIssue,
  platform = Platform.android,
  description = '',
  details = '',
  attachments = [],
}) => {
  const dispatch = useDispatch()

  const handleConfirm = () => dispatch(addIssue({
    status: Status.opened,
    created: dayjs().unix(),
    updated: null,
    deadline: null,
    platform,
    message: description,
    deviceInfo: details,
    attachments,
  }))

  return (
    <div className="app-layout">
      <header className="app-layout__header">
        <HeaderNav>
          <Link className="header-nav-btn header-nav-btn--icon" to="/ticket-new" onClick = { handleGoBackNewIssue }>
            <Back />
          </Link>
          <strong className="header-nav__title">Подтверждение</strong>

          <Link className="header-nav-btn" to="/tickets">
            <span className="header-nav-btn__text" onClick={handleConfirm}>Готово</span>
          </Link>
        </HeaderNav>
      </header>

      <main className="app-layout__main">
        <div className="app-form">
          <div className="app-form__group">
            <h3 className="app-title app-title--h4">Платформа</h3>
            <span className="app-text app-text--desc">{PlatformText[platform]}</span>
          </div>

          <div className="app-form__group">
            <h3 className="app-title app-title--h4 app-form__divider-inner">Описание</h3>
            <span className="app-text app-text--desc">{description}</span>
          </div>

          {details && <div className="app-form__group">
            <h3 className="app-title app-title--h4 app-form__divider-inner">Комментарий</h3>
            <span className="app-text app-text--desc">{details}</span>
          </div>}

          {attachments.length ? (<div className="app-form__group app-form__group--files">
            <h3 className="app-title app-title--h4 app-form__divider-inner">Вложения</h3>

            {attachments.map((attachment: any) => (
            <div className="app-file">
              <svg
                className="app-file__close"
                viewBox="0 0 18 18"
                width="18"
                height="18"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M11 9l6.4-6.5a1 1 0 000-1.3l-.6-.6a1 1 0 00-.6-.3 1 1 0 00-.7.3L9 7 2.5.6a1 1 0 00-.7-.3 1 1 0 00-.6.3l-.6.6a1 1 0 000 1.3L7 9 .6 15.5a1 1 0 00-.3.7c0 .2.1.5.3.6l.6.6c.1.2.4.3.6.3.3 0 .5-.1.7-.3L9 11l6.5 6.5c.2.2.4.3.7.3.2 0 .5-.1.6-.3l.6-.6c.2-.1.3-.4.3-.6a1 1 0 00-.3-.7L11 9z"
                  fill="currentColor"
                />
                </svg>
                <div className="app-file__title">{attachment?.name}</div>
                <div className="app-file__size">{attachment?.size}</div>
              </div>
            ))}
          </div>) : null}
        </div>
      </main>
    </div>
  )
}

export default IssueConfirmation