import Dropdown from 'react-dropdown'
import TextareaAutosize from 'react-autosize-textarea'
import './FormField.scss'
import { FC } from 'react'

interface Props {
  type: string,
  placeholder?: string,
  value?: string,
  description?: string,
  onChange?: any,
  options?: any,
}

const FormField:FC<Props> = ({ type, placeholder, value, description, onChange, options }) => {
  return (
    <div className={`form-field form-field--${type}`}>
      {type === 'dropdown' && (
        <Dropdown
          className="form-field__field form-field__field--dropdown"
          options={options}
          placeholder={placeholder}
          onChange={onChange}
          value={value}
        />
      )}

      {type === 'text' && (
        <TextareaAutosize
          className="form-field__field"
          defaultValue={value}
          placeholder={placeholder}
          onChange={onChange}
        />
      )}

      {description && <div className="form-field__description" dangerouslySetInnerHTML={{ __html: description }} />}
    </div>
  )
}

export default FormField
