import { FunctionComponent, useState } from 'react'
import { Platform } from '../../types'
import IssueConfirmation from './IssueConfirmation'
import IssueNew from './IssueNew'

const Issue:FunctionComponent<{}> = () => {
  const [platform, setPlatform] = useState<Platform>()
  const [description, setDescription] = useState<string>('')
  const [details, setDetailInfo] = useState<string>('')
  const [isConfirmStep, setConfirmStep] = useState<boolean>(false)
  const [attachments, setAttachments] = useState<[]>([])

  const handleGoToConfirm = () => {
    setConfirmStep(true)
  }
  const handleGoBackNewIssue = () => {
    setConfirmStep(false)
  }

  return isConfirmStep ?
    <IssueConfirmation
      handleGoBackNewIssue={handleGoBackNewIssue}
      platform = {platform}
      description = {description}
      details = {details}
      attachments = {attachments}
    /> :
    <IssueNew
      handleGoToConfirm={handleGoToConfirm}
      setPlatform={setPlatform}
      setDescription={setDescription}
      setDetailInfo={setDetailInfo}
      setAttachments={setAttachments}
      platform = {platform}
      description = {description}
      details = {details}
      attachments = {attachments}
    />
}

export default Issue